import React from "react";
import createHistory from "history/createBrowserHistory";
import Routes from './Routes';
import {Link} from "react-router-dom";
import { BrowserRouter} from 'react-router-dom';

// Create a history of your choosing (we're using a browser history in this case)
//const history = createHistory();
const history = createHistory({
  basename: process.env.PUBLIC_URL,
});

export class RouterTest extends React.Component {

  // this constructor is necessary to make the props work
  constructor(props) {
      super(props);
  }

  render() {
    console.log(process.env.PUBLIC_URL);
    return (
 

    <BrowserRouter history={history}  basename={'/my-app/'}>
      <div>
      <div>
                        <Link to="/help" >Help</Link><br />
                        <Link to="/parts/123">Parts</Link><br />
                        <div className="jumbotron">
                            <Routes />
                        </div>
                    </div>
      </div>
    </BrowserRouter>

);
  }
}



